import React, {useCallback, useContext, useEffect, useRef, useState} from 'react';
import PropTypes from 'prop-types';
import Grid from "@material-ui/core/Grid";
import {makeStyles} from "@material-ui/styles";
import Avatar from "@material-ui/core/Avatar";
import KmStatusMe from "../Dashboard/KmStatusMe";
import API from "../../config/API";
import {userService} from "../../config/constans";
import {MdAddAPhoto} from "react-icons/md";
import IconButton from "@material-ui/core/IconButton";
import {Person} from "@material-ui/icons";
import Dialog from "@material-ui/core/Dialog";
import "react-image-crop/dist/ReactCrop.css";
import DevImageCrop from "../../components/common/DevImageCrop";
import {BackDropContext} from "../../config/BackDropContext";
import {AiOutlineSetting} from "react-icons/ai";
import Popper from "@material-ui/core/Popper";
import Grow from "@material-ui/core/Grow";
import Paper from "@material-ui/core/Paper";
import ClickAwayListener from "@material-ui/core/ClickAwayListener";
import MenuList from "@material-ui/core/MenuList";
import MenuItem from "@material-ui/core/MenuItem";
import InputAdornment from "@material-ui/core/InputAdornment";
import Visibility from "@material-ui/icons/Visibility";
import VisibilityOff from "@material-ui/icons/VisibilityOff";
import DevTextField from "../../components/common/DevTextField";
import Button from "@material-ui/core/Button";
import {useSnackbar} from "notistack";

PersonInfo.propTypes = {
    userId: PropTypes.string
};
const styles = makeStyles(() => ({
    card: {
        "borderRadius": "10px",
        "boxShadow": "0 4px 16px 0 rgba(37, 56, 88, 0.1)",
        "backgroundColor": "#ffffff",
        minHeight: 240,
        textAlign: "center",
        padding: 16
    },
    avatar: {
        width: 94,
        height: 94,
        "boxShadow": "0 3px 6px 0 rgba(37, 56, 88, 0.2)"
    },
    fullName: {
        marginTop: 12,
        "fontFamily": "'IranSans'",
        "fontSize": "18px",
        "fontWeight": "bold",
        "fontStretch": "normal",
        "fontStyle": "normal",
        "lineHeight": "1.56",
        "letterSpacing": "normal",
        "textAlign": "center",
        "color": "#344563"
    },
    jobTitle: {
        "fontFamily": "'IranSans'",
        "fontSize": "12px",
        "fontWeight": "normal",
        "fontStretch": "normal",
        "fontStyle": "normal",
        "lineHeight": "1.58",
        "letterSpacing": "normal",
        "textAlign": "center",
        "color": "#42516e"
    },
    numFollowers: {
        "fontFamily": "'IranSansFaNum'",
        "fontSize": "24px",
        "fontWeight": "bold",
        "fontStretch": "normal",
        "fontStyle": "normal",
        "lineHeight": "1.58",
        "letterSpacing": "normal",
        "textAlign": "center",
        "color": "#42516e"
    },
    titleFollowers: {
        "fontFamily": "'IranSans'",
        "fontSize": "14px",
        "fontWeight": "normal",
        "fontStretch": "normal",
        "fontStyle": "normal",
        "lineHeight": "1.57",
        "letterSpacing": "normal",
        "textAlign": "center",
        "color": "#c0c7d1"
    },
    buttonEdit: {
        "fontFamily": "'IranSans'",
        "fontSize": "14px",
        "fontWeight": "normal",
        "fontStretch": "normal",
        "fontStyle": "normal",
        "lineHeight": "1.57",
        "letterSpacing": "normal",
        "textAlign": "center",
        "color": "#42516e",
        width: 135,
        height: 32,
        "borderRadius": "5px !important",
        "border": "solid 1px #42516e !important",
        "backgroundColor": "#ffffff !important"
    },
    MuiIconButtonRoot: {
        padding: 0
    },
    buttonFollow: {
        "fontSize": "14px",
        "fontWeight": "normal",
        "fontStretch": "normal",
        "fontStyle": "normal",
        "lineHeight": "1.57",
        "letterSpacing": "normal",
        "textAlign": "center",
        "color": "#ffffff",
        "borderRadius": "5px !important",
        "backgroundColor": "#2684fe !important",
        width: 97,
        height: 32
    },
    textField: {
        marginTop: 4,
        marginBottom: 4,
        height: 34
    },
    inputBaseFullWidth: {
        height: "100%"
    },
    btnSave: {
        "height": "32px", "borderRadius": "5px",
        "backgroundColor": "#3ac3ff  !important",
        color: "#fff",
    },
    btnCancel: {
        "height": "32px", "borderRadius": "5px",
        backgroundColor: "#ffc9c9  !important",
        color: "#fff",
    },
}));


function PersonInfo(props) {
    const [info, setInfo] = useState({
        jobTitle: "--",
        fullName: "--",
        followers: 5,
        following: 10
    });
    const [password, setPassword] = useState("");
    const [rePassword, setRePassword] = useState("");
    const [currentPassword, setCurrentPassword] = useState("");
    const [showPassword, setShowPassword] = useState(false);
    const [image, setImage] = useState(null);
    const [showImage, setShowImage] = useState(false);
    const [showDialogChangePassword, setShowDialogChangePassword] = useState(false);
    const {dispatch} = useContext(BackDropContext);
    const [openMenu, setOpenMenu] = useState(false);
    const anchorRef = useRef(null);
    const inputRef = useRef(null);
    const classes = styles();
    const {userId} = props;
    const {enqueueSnackbar} = useSnackbar();

    function handlePassword(e) {
        setPassword(e.target.value)
    }

    function handleRePassword(e) {
        setRePassword(e.target.value)
    }

    function handleCurrentPassword(e) {
        setCurrentPassword(e.target.value)
    }

    function handleClickShowPassword() {
        setShowPassword(prevState => !prevState);
    }

    const handleOpenChangePassword = () => {
        setShowDialogChangePassword(true);
    };
    const handleCloseChangePassword = () => {
        setPassword("");
        setRePassword("");
        setShowPassword(false);
        setShowDialogChangePassword(false);
    };
    const handleToggle = () => {
        setOpenMenu((prevOpen) => !prevOpen);
    };

    const handleClose = () => {
        setOpenMenu(false);
    };

    function handleListKeyDown(event) {
        if (event.key === 'Tab') {
            event.preventDefault();
            setOpenMenu(false);
        }
    }


    function uploadAvatar(form) {
        API({method: "POST", data: form, url: userService.postAvatarByUid(userId)}, dispatch).then(res => {
            if (res.status === 200) {
                getAvatar();
                // window.location.reload();
            }
        })

    }

    function getUser(Uid) {
        API({url: userService.getInfoByUid(Uid)}).then(res => {
            setInfo(prevState => ({...prevState, ...res.data}))
        });
    }

    const getAvatar = useCallback(() => {
        API({url: userService.getJsonAvatarByUid(userId, 100, 100)}).then(res => {
            if (res.status === 200) {
                setImage(res.data.avatar)
            }
        });
    }, [userId]);

    useEffect(() => {
        getUser(userId);
        getAvatar();
    }, [userId, getAvatar]);


    function closeImage() {
        setShowImage(false);
    }

    function submitFormChangePassword() {
        if (password && rePassword === password && currentPassword) {
            API({
                method: "PUT",
                data: {password: password, oldPassword: currentPassword},
                url: userService.putChangePassword(localStorage.getItem("userId"))
            }, dispatch).then(res => {
                if (res.status === 200) {
                    enqueueSnackbar("رمزعبور با موفقیت تغییر کرد", {
                        variant: "success", anchorOrigin: {
                            vertical: 'bottom',
                            horizontal: 'center',
                        }
                    });
                } else {
                    console.log(res);
                }
            }).catch((e) => {
                    enqueueSnackbar(e.response.data.message, {
                        variant: "error", anchorOrigin: {
                            vertical: 'bottom',
                            horizontal: 'center',
                        }
                    });
            });
        } else {
            enqueueSnackbar("رمزعبور یکسان نمی باشد", {
                variant: "error", anchorOrigin: {
                    vertical: 'bottom',
                    horizontal: 'center',
                }
            });
        }
    }

    const editableImage = JSON.parse(localStorage.getItem("user")).userId === userId ? {
        onClick: () => {
            inputRef.current.click()
        }
    } : () => ({});

    return (<>
            <Dialog
                fullWidth
                open={showDialogChangePassword}
                onClose={handleCloseChangePassword}
            >
                <Grid style={{padding: 8}} spacing={1} container>
                    <Grid xs={12} item><h6>تغییر رمز عبور</h6></Grid>
                    <Grid xs={12} item>
                        <DevTextField
                            autoComplete={false}
                            margin={"none"}
                            className={classes.textField}
                            color={"primary"}
                            variant="outlined"
                            onChange={handleCurrentPassword}
                            required
                            fullWidth
                            name="currentPassword"
                            label="رمزعبور فعلی"
                            value={currentPassword}
                            type={showPassword ? 'text' : 'password'}
                            id="currentPassword"
                            InputProps={{
                                classes: {input: classes.inputBaseFullWidth},
                                endAdornment: <InputAdornment position="end">
                                    <IconButton
                                        aria-label="currentPasswordToggle"
                                        onClick={handleClickShowPassword}
                                        edge="end"
                                    >
                                        {showPassword ? <Visibility/> : <VisibilityOff/>}
                                    </IconButton>
                                </InputAdornment>
                            }}
                            labelwidth={70}
                        />
                    </Grid>
                    <Grid xs={12} item>
                        <DevTextField
                            autoComplete={false}
                            margin={"none"}
                            className={classes.textField}
                            color={"primary"}
                            variant="outlined"
                            onChange={handlePassword}
                            required
                            fullWidth
                            name="changePassword"
                            label="رمزعبور جدید"
                            value={password}
                            type={showPassword ? 'text' : 'password'}
                            id="changePassword"
                            InputProps={{
                                classes: {input: classes.inputBaseFullWidth}
                            }}
                            labelwidth={70}
                        />
                    </Grid>
                    <Grid xs={12} item>
                        <DevTextField
                            autoComplete={false}
                            margin={"none"}
                            className={classes.textField}
                            color={"primary"}
                            variant="outlined"
                            onChange={handleRePassword}
                            required
                            fullWidth
                            name="reChangePassword"
                            label="تکرار رمزعبور جدید"
                            value={rePassword}
                            type={showPassword ? 'text' : 'password'}
                            id="reChangePassword"
                            InputProps={{
                                classes: {input: classes.inputBaseFullWidth},
                            }}
                            labelwidth={70}
                        />
                    </Grid>
                    <Grid xs={6} item/>
                    <Grid xs={3} item>
                        <Button onClick={handleCloseChangePassword} fullWidth classes={{root: classes.btnCancel}}
                                variant={"contained"}>انصراف</Button>
                    </Grid>
                    <Grid xs={3} item>
                        <Button onClick={submitFormChangePassword} fullWidth classes={{root: classes.btnSave}}
                                variant={"contained"}>تایید</Button>
                    </Grid>
                </Grid>
            </Dialog>
            <Dialog
                open={showImage}
                onClose={closeImage}
            >
                <img alt="avatar" src={"data:image/png;base64," + image}/>
            </Dialog>
            <Grid container alignItems={"center"} className={classes.card}>
                <div style={{
                    width: "100%",
                    textAlign: "left"
                }}>
                    <IconButton onClick={handleToggle} size={"small"}>
                        <AiOutlineSetting/>
                    </IconButton>
                    <div style={{position: "relative"}}>
                        <Popper style={{position: "absolute"}} open={openMenu} anchorEl={anchorRef.current}
                                role={undefined} transition disablePortal>
                            {({TransitionProps, placement}) => (
                                <Grow
                                    {...TransitionProps}
                                    style={{transformOrigin: placement === 'bottom' ? 'center top' : 'center bottom'}}
                                >
                                    <Paper>
                                        <ClickAwayListener onClickAway={handleClose}>
                                            <MenuList autoFocusItem={openMenu} id="menu-list-grow"
                                                      onKeyDown={handleListKeyDown}>
                                                <MenuItem onClick={handleOpenChangePassword}>تغییر رمز عبور</MenuItem>
                                            </MenuList>
                                        </ClickAwayListener>
                                    </Paper>
                                </Grow>
                            )}
                        </Popper>
                    </div>
                </div>
                <Grid xs={4} item>
                    {/*<div className={classes.titleFollowers}>دنبال کننده</div>*/}
                    {/*<div className={classes.numFollowers}>{info.followers}</div>*/}
                </Grid>
                <Grid xs={4} item>
                    {info.userId || true ? <>
                            <IconButton {...(image ? {onClick: undefined} : editableImage)}
                                        classes={{root: classes.MuiIconButtonRoot}}>
                                <Avatar className={classes.avatar}
                                        alt="User_Profile"
                                        src={"data:image/png;base64," + image}>
                                    <Person style={{fontSize: 40}}/>
                                </Avatar>
                            </IconButton>

                            {JSON.parse(localStorage.getItem("user")).userId === userId ? <>
                                <DevImageCrop maxSizeMB={0.5} circularCrop={true} uploadHandler={uploadAvatar}
                                              inputRef={inputRef}/>
                                <IconButton  {...editableImage} size={"small"}><MdAddAPhoto
                                    style={{fontSize: 18}}/></IconButton> </> : ""}
                        </>
                        : null}

                </Grid>
                <Grid xs={4} item>
                    {/*<div className={classes.titleFollowers}>دنبال شونده</div>*/}
                    {/*<div className={classes.numFollowers}>{info.following}</div>*/}
                </Grid>
                <Grid xs={12} item>
                    <div className={classes.fullName}>{info.fullName}</div>
                    <div className={classes.jobTitle}>{info.jobTitle}</div>
                </Grid>
                <Grid xs={12} item>
                    <KmStatusMe userId={userId} inProfile={true}/>
                </Grid>
                <Grid xs={12} item>
                    {JSON.parse(localStorage.getItem("user")).userId === userId ?
                        "" : ""
                        // <Button className={classes.buttonFollow} variant={"contained"}>دنبال کردن</Button>
                    }
                </Grid>
            </Grid>
        </>
    );
}

export default PersonInfo;