import React, {useCallback, useState} from 'react';
import Grid from "@material-ui/core/Grid";
import FolderCard from "./FolderCard";
import Button from "@material-ui/core/Button";
import {MdCreateNewFolder} from "react-icons/md";
import {makeStyles} from "@material-ui/styles";
import DevSearchBox from "../../components/common/DevSearchBox";

const styles = makeStyles((theme) => ({
    textButtonAddFolder: {
        "fontFamily": "'IranSans'",
        "fontSize": "14px",
        "fontWeight": "normal",
        "fontStretch": "normal",
        "fontStyle": "normal",
        "lineHeight": "1.58",
        "letterSpacing": "normal",
        "textAlign": "center",
        "color": "#2684fe"
    }, iconButtonAddFolder: {
        "fontSize": "24px",
        "fontWeight": "normal",
        "fontStretch": "normal",
        "fontStyle": "normal",
        "lineHeight": "1.58",
        "letterSpacing": "normal",
        "textAlign": "center",
        "color": "#2684fe"
    },
}));

function Index() {
    // const [data, setData] = useState([]);
    const classes = styles();
    const [, setKeyword] = useState([]);
    const [docs,] = useState({
        content: [
            {
                id: 1,
                title: "فرانت اند",
                createAt: new Date().getTime() - 61102,
                author: "سیدمحمدرضاشوبیری",
                numFile: 36
            },
            {id: 2, title: "بک اند", createAt: new Date().getTime() - 51402, author: "سهیل قلمکاری", numFile: 81},
            {id: 4, title: "داده کاوی", createAt: new Date().getTime() - 41102, author: "علیرضا قاسمی", numFile: 5},
            {id: 3, title: "هوش مصنوعی", createAt: new Date().getTime() - 20002, author: "محمد پدران", numFile: 127},
            {id: 5, title: "متفرقه", createAt: new Date().getTime() - 78001, author: "ادمین سیستم", numFile: 1},
            {id: 6, title: "مدیریت صنعتی", createAt: new Date().getTime() - 30004, author: "فرشید صالحی", numFile: 3},
            {
                id: 7,
                title: "تکنیک های مدیریت",
                createAt: new Date().getTime() - 91102,
                author: "فرشید صالحی",
                numFile: 56
            },
        ], number: -1, totalElements: 0
    });

    const searchMethod = useCallback((keyword) => {
        setKeyword(keyword);
    }, []);
    return (
        <Grid container>
            <Grid xs={12} item>
                <DevSearchBox searchMethod={searchMethod}/>
            </Grid>
            <Grid justify={"space-between"} alignItems={"center"} container item xs={12}>
                <Grid style={{marginTop: 12, marginBottom: 12}} item xs={6}>
                    <Button className={classes.textButtonAddFolder}>
                        <MdCreateNewFolder className={classes.iconButtonAddFolder}/>
                        افزودن پوشه جدید
                    </Button>
                </Grid>
                <Grid item xs={6}>
                </Grid>
            </Grid>
            <Grid xs={12} item className="animate__slideInUp animate__animated"
                  alignItems={"center"}
                  container>
                {/*<InfiniteScroll*/}
                {/*    initialScrollY={200}*/}
                {/*    height={800}*/}
                {/*    style={{minWidth: "275px"}}*/}
                {/*    dataLength={listsWiki.content.length}*/}
                {/*    next={() => {*/}
                {/*        fetchDataWiki(listsWiki.number + 1)*/}
                {/*    }}*/}
                {/*    hasMore={!listsWiki.last}*/}
                {/*    loader={listsWiki.content.length > 0 &&*/}
                {/*    <div style={{textAlign: "center",}}><CircularProgress/></div>}*/}
                {/*>*/}
                {docs.content.map(doc => {
                        if (doc) {
                            return (<Grid key={doc.id} xs={12} item>
                                <FolderCard doc={doc}/>
                            </Grid>)
                        } else {
                            return "";
                        }
                    }
                )}
                <Grid xs={12} style={{height: 70}} item/>
                {/*</InfiniteScroll>*/}

            </Grid>
        </Grid>
    );
}

export default Index;
