import React from 'react';
import PropTypes from 'prop-types';
import {makeStyles} from "@material-ui/styles";
import Grid from "@material-ui/core/Grid";
import moment from "moment";
import IconButton from "@material-ui/core/IconButton";
import {MdMoreHoriz} from "react-icons/md";
import ListItem from "@material-ui/core/ListItem";

FolderCard.propTypes = {
    doc: PropTypes.object.isRequired
};
const styles = makeStyles((theme) => ({
    container: {
        "marginBottom": 12,
        "minHeight": "70px",
        "padding": "10px",
        "borderRadius": "10px",
        "boxShadow": "0 4px 16px 0 rgba(37, 56, 88, 0.1)",
        "backgroundColor": "#ffffff"
    },
    title: {
        "fontFamily": "'IranSans'",
        "fontSize": "12px",
        "fontWeight": "bold",
        "fontStretch": "normal",
        "fontStyle": "normal",
        "lineHeight": "1.57",
        "letterSpacing": "normal",
        "color": "#42516e",
        "textAlign": "center",
    },
    author: {
        "fontFamily": "'IranSans'",
        "fontSize": "9px",
        "fontWeight": "normal",
        "fontStretch": "normal",
        "fontStyle": "normal",
        "lineHeight": "1.5",
        "letterSpacing": "normal",
        "textAlign": "center",
        "color": "#42516e"
    },
    numFile: {
        "fontFamily": "'IranSansFaNum'",
        "fontSize": "9px",
        "fontWeight": "normal",
        "fontStretch": "normal",
        "fontStyle": "normal",
        "lineHeight": "1.5",
        "letterSpacing": "normal",
        "textAlign": "center",
        "color": "#c0c7d1"
    },
    createAt: {
        "fontFamily": "'IranSansFaNum'",
        "fontSize": "9px",
        "fontWeight": "normal",
        "fontStretch": "normal",
        "fontStyle": "normal",
        "lineHeight": "1.5",
        "letterSpacing": "normal",
        "textAlign": "right",
        "color": "#c0c7d1"
    },
    more: {
        "textAlign": "right",
    },
    childItem: {
        minHeight: "inherit"
    },
    imageCover: {
        "backgroundColor": "#feefb2",
        width: 60,
        height: 60,
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        borderRadius: 32
    },
    icon: {
        width: 32,
        height: 32,
    }
}));

function FolderCard(props) {
    const {title, createAt, author, numFile} = props.doc;
    const classes = styles();
    return (<ListItem button onClick={undefined} className={classes.container}>
            <Grid className={classes.childItem} alignItems={"center"} justify={"center"} container>
                <Grid xs={3} item>
                    <div className={classes.imageCover}>
                        <img alt="folder-ico" className={classes.icon}
                             src={"data:image/png;base64,"}/>
                    </div>
                </Grid>
                <Grid className={classes.childItem} direction={"column"} alignItems={"flex-start"}
                      justify={"space-around"}
                      container xs={4} item>
                    <Grid className={classes.title} xs={12} item>{title}</Grid>
                    <Grid className={classes.author} xs={12} item>مالک: {author}</Grid>
                    <Grid xs={12} className={classes.numFile} item>تعداد‌فایل‌ها: {numFile}</Grid>

                </Grid>
                <Grid className={classes.childItem} direction={"column"} alignItems={"flex-end"}
                      justify={"space-between"}
                      container xs={5} item>
                    <Grid className={classes.more} xs={12} item>
                        <IconButton size={"small"}>
                            <MdMoreHoriz style={{color: "#c0c7d1"}}/>
                        </IconButton>
                    </Grid>
                    <Grid className={classes.createAt} xs={12}
                          item>{moment(new Date(createAt - 250000)).fromNow()}</Grid>

                </Grid>
            </Grid>
        </ListItem>
    );
}

export default FolderCard;